.footer {
  min-height: 50vh;
  width: 100vw;
}

.footer-display {
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer-card {
  box-shadow: 0px 2px rgb(177, 175, 175);
  padding: 2rem;
  width: 70vw;
}

.footer-logo-container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  cursor: pointer;
}

.footer-heading {
  font-size: 2.1rem;
}

.footer-link {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem;
}

ul {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  gap: 2rem;
  font-size: 1.3rem;
  list-style-type: none;
  cursor: pointer;
  width: 30rem;
}

li {
  position: relative;
  color: black;
}

li::after {
  content: "";
  position: absolute;
  width: 80%;
  height: 2px;
  background-color: rgb(83, 82, 82);
  bottom: -5px;
  left: 50%;
  transform: translateX(-50%) scaleX(0);
  transform-origin: bottom center;
  transition: transform 0.7s ease-in-out;
}

li:hover::after {
  transform: translateX(-50%) scaleX(1);
  transform-origin: bottom center;
}

li:hover {
  color: rgb(134, 133, 133);
}

.socials {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  margin: 1rem;
  cursor: pointer;
}

.copyright {
  text-align: center;
  margin: 2rem;
}

@media screen and (max-width: 650px) {
  .footer {
    transform: scale(0.9);
  }
}

@media screen and (max-width: 560px) {
  .footer {
    transform: scale(0.8);
  }
}

@media screen and (max-width: 480px) {
  .footer {
    transform: scale(0.7);
  }
}
